@import url(https://pl.allfont.net/allfont.css?fonts=comic-sans-ms);

.App {
  font-size: 2vh;
  text-align: center;
  font-family: 'Comic Sans MS', Comic Sans MS, Comic Sans, cursive;
}

.btn-group button {
  font-family: 'Comic Sans MS', Comic Sans MS, Comic Sans, cursive;
  margin-bottom: 1vh;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  cursor: pointer;
  width: 150px;
  display: block;
  font-size: 2vh;
  width: 20vh;

}

.btn-group {
  position: absolute;
  left: 50vw;
  top: 15vh;
  -ms-transform: translate(-50%, 0%);
  transform: translate(-50%, 0%);
}